import React, { useContext } from 'react'
import LanguageVersionInfo from '../../data/LangsVersions.json'
import AvailableContent from '../../data/availableContent.json'
import {Dropdown} from 'primereact/dropdown';
import AppContext from '../../contextAPI/AppContext'
import styles from './Menu.module.css'
import { navigate } from "@reach/router";
import { StaticQuery, graphql } from 'gatsby'

import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import InboxIcon from '@material-ui/icons/Inbox';
import Home from '@material-ui/icons/Home';
import LanguageSelect from '../AutoComplete/LanguageSelect'
import VersionSelect from '../AutoComplete/VersionSelect'
import KeyPassageSelect from '../AutoComplete/KeyPassageSelect'
import Avatar from '@material-ui/core/Avatar';
import SVReaderLogo from '../../images/SVLogo.jpeg'
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';

const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      height: '100%',
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
      overflowY: 'auto'
    },
    avatar: {
      marginRight: 10,
    },
    homeIcon: {
      left: 0
    },
    keyPassages: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    sphereSwitch: {
      width: '100%'
    }
  }));

// function ListItemLink(props) {
// return <ListItem button component="a" {...props} />;
// }

const Menu = (props) => {
    const classes = useStyles();
    const receivedAppContext = useContext(AppContext)

    return (
        <div className={classes.root}>
            <List component="nav" aria-label="Main mailbox folders">
                <ListItem onClick={() => {
                        props.closeMenu()
                        navigate('/')
                    }} >
                    <Avatar alt="Remy Sharp" src={SVReaderLogo} className={classes.avatar} />
                    <ListItemText primary="SourceView Bible Reader" />
                    <ListItemIcon className={classes.homeIcon}>
                        <Home />
                    </ListItemIcon>
                </ListItem>
                <Divider />
                <ListItem>
                    <LanguageSelect location={props.location} />
                </ListItem>
                <ListItem>
                    <VersionSelect location={props.location} />
                </ListItem>
                {(receivedAppContext.version === 'NLT') ? (<React.Fragment>
                    <Divider />
                    <ListItem className={classes.keyPassages}>
                        <FormGroup row className={classes.sphereSwitch}>
                            <FormControlLabel
                                label="Sphere Key Passages"
                                labelPlacement="start"
                                control={
                                <Switch checked={receivedAppContext.showSpheres} onChange={() => receivedAppContext.updateShowSpheres(!receivedAppContext.showSpheres)} value={receivedAppContext.showSpheres} />
                                }
                            />
                        </FormGroup>
                    </ListItem>
                    {(receivedAppContext.showSpheres) ? (<ListItem><KeyPassageSelect closeMenu={props.closeMenu} /></ListItem>) : null}
                </React.Fragment>) : null}
            </List>
        </div>)
}

export default Menu;