import { Link } from "gatsby"
import PropTypes from "prop-types"
import React, { useContext, useState } from "react"
import AppContext from '../../contextAPI/AppContext'
import { navigate } from "@reach/router";

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Info from '@material-ui/icons/Info';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import Search from '@material-ui/icons/Search';
import SearchPanel from '../SearchPanel/SearchPanel'
import Menu from '../Menu/Menu'
import Drawer from '@material-ui/core/Drawer';

import GetSegment from '../../scripts/NavigateToSegment'

const useStyles = makeStyles({
  toolBar: {
    justifyContent: 'space-between',
    // '& div': {
    //   justifyContent: 'space-evenly',
    //   border: 'red solid 2px',
    // }
  },
  icons: {
    margin: '3px'
  }
})

const Header = ({ siteTitle, showIcons, currentLocation }) => {
  const classes = useStyles();
  // This controls the visibility of chapter and verse references
  const receivedAppContext = useContext(AppContext);
  const [ menu, updateMenu ] = useState(false)
  const [ searchMenu, updateSearchMenu ] = useState(false)
  const interlinearOption = (receivedAppContext.version === 'GNA' || receivedAppContext.version === 'WLC') ? true : false
  // change all updateDisplayRef to manually set true or false

  return (
    <AppBar position="fixed">
    <Toolbar className={classes.toolBar}>
          <div>
            <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => updateMenu(!menu)}>
              <MenuIcon />
            </IconButton>
          </div>
          <div>
            <Link
              to="/"
              style={{
                color: `white`,
                textDecoration: `none`,
                flex: '10'
              }}
            >
              {siteTitle}
            </Link>
          </div>
          <div>
            {(interlinearOption && showIcons) ? (
              <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => receivedAppContext.updateInterlinear(!receivedAppContext.interlinear)}>
                <Info />
              </IconButton>
            ) : null}
            {(showIcons) ? (<IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => receivedAppContext.updateDisplayRef(!receivedAppContext.displayRef)}>
              {(receivedAppContext.displayRef) ? (<Visibility />) : 
              (<VisibilityOff />)}
            </IconButton>) : null}
            <IconButton className={classes.icons} size="small" color="inherit" aria-label="Menu" onClick={() => updateSearchMenu(!searchMenu)}>
              <Search />
            </IconButton>
          </div>
</Toolbar>
<Drawer 
  className={classes.drawer}
  open={menu} 
  anchor="left"
  width="75%"
  maxWidth="75%"
  overflowX="auto"
  onClose={() => updateMenu(false)}>
    <Menu location={currentLocation} closeMenu={() => updateMenu(false)} />
</Drawer>
<Drawer 
  className={classes.drawer}
  open={searchMenu} 
  anchor="right"
  width="75%"
  onClose={() => updateSearchMenu(false)}>
    <SearchPanel 
      currentLocation={currentLocation.pathname}
      closeSearchMenu={() => updateSearchMenu(false)}/>
</Drawer>
</AppBar>
)}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
